import { createRouter, createWebHashHistory } from 'vue-router'
import index from '../views'
import home from '../views/pages/home.vue'
import login from '../views/pages/login.vue'
import newsList from '@/views/pages/news/list'
import duzheList from '@/views/pages/duzhe/list'
import duzheDetail from '@/views/pages/duzhe/formModel'
import duzheAdd from '@/views/pages/duzhe/formAdd'
import duzheRegister from '@/views/pages/duzhe/register'
import duzheCenter from '@/views/pages/duzhe/center'
import yuelanshixinxiList from '@/views/pages/yuelanshixinxi/list'
import yuelanshixinxiDetail from '@/views/pages/yuelanshixinxi/formModel'
import yuelanshixinxiAdd from '@/views/pages/yuelanshixinxi/formAdd'
import storeupList from '@/views/pages/storeup/list'
import zuoweiyuyueList from '@/views/pages/zuoweiyuyue/list'
import zuoweiyuyueDetail from '@/views/pages/zuoweiyuyue/formModel'
import zuoweiyuyueAdd from '@/views/pages/zuoweiyuyue/formAdd'
import messagesList from '@/views/pages/messages/list'
import tushufenleiList from '@/views/pages/tushufenlei/list'
import tushufenleiDetail from '@/views/pages/tushufenlei/formModel'
import tushufenleiAdd from '@/views/pages/tushufenlei/formAdd'
import tushuxinxiList from '@/views/pages/tushuxinxi/list'
import tushuxinxiDetail from '@/views/pages/tushuxinxi/formModel'
import tushuxinxiAdd from '@/views/pages/tushuxinxi/formAdd'
import tushujieyueList from '@/views/pages/tushujieyue/list'
import tushujieyueDetail from '@/views/pages/tushujieyue/formModel'
import tushujieyueAdd from '@/views/pages/tushujieyue/formAdd'
import tushuguihaiList from '@/views/pages/tushuguihai/list'
import tushuguihaiDetail from '@/views/pages/tushuguihai/formModel'
import tushuguihaiAdd from '@/views/pages/tushuguihai/formAdd'

const routes = [{
		path: '/',
		redirect: '/index/home'
	},
	{
		path: '/index',
		component: index,
		children: [{
			path: 'home',
			component: home
		}
		, {
			path: 'newsList',
			component: newsList
		}
		, {
			path: 'duzheList',
			component: duzheList
		}, {
			path: 'duzheDetail',
			component: duzheDetail
		}, {
			path: 'duzheAdd',
			component: duzheAdd
		}
		, {
			path: 'duzheCenter',
			component: duzheCenter
		}
		, {
			path: 'yuelanshixinxiList',
			component: yuelanshixinxiList
		}, {
			path: 'yuelanshixinxiDetail',
			component: yuelanshixinxiDetail
		}, {
			path: 'yuelanshixinxiAdd',
			component: yuelanshixinxiAdd
		}
		, {
			path: 'storeupList',
			component: storeupList
		}
		, {
			path: 'zuoweiyuyueList',
			component: zuoweiyuyueList
		}, {
			path: 'zuoweiyuyueDetail',
			component: zuoweiyuyueDetail
		}, {
			path: 'zuoweiyuyueAdd',
			component: zuoweiyuyueAdd
		}
		, {
			path: 'messagesList',
			component: messagesList
		}
		, {
			path: 'tushufenleiList',
			component: tushufenleiList
		}, {
			path: 'tushufenleiDetail',
			component: tushufenleiDetail
		}, {
			path: 'tushufenleiAdd',
			component: tushufenleiAdd
		}
		, {
			path: 'tushuxinxiList',
			component: tushuxinxiList
		}, {
			path: 'tushuxinxiDetail',
			component: tushuxinxiDetail
		}, {
			path: 'tushuxinxiAdd',
			component: tushuxinxiAdd
		}
		, {
			path: 'tushujieyueList',
			component: tushujieyueList
		}, {
			path: 'tushujieyueDetail',
			component: tushujieyueDetail
		}, {
			path: 'tushujieyueAdd',
			component: tushujieyueAdd
		}
		, {
			path: 'tushuguihaiList',
			component: tushuguihaiList
		}, {
			path: 'tushuguihaiDetail',
			component: tushuguihaiDetail
		}, {
			path: 'tushuguihaiAdd',
			component: tushuguihaiAdd
		}
		]
	},
	{
		path: '/login',
		component: login
	}
	,{
		path: '/duzheRegister',
		component: duzheRegister
	}
]

const router = createRouter({
  history: createWebHashHistory(process.env.BASE_URL),
  routes
})

export default router
